@import url("https://fonts.googleapis.com/css?family=Montserrat:200,300,700");

:root {
  --border-line: #222222;
  --bg-dark: #222222;
  --bg-shade: #333333;
  --text-light: #cecece;
  --text-title: #efd96f;
  --text-shade: #ffa000;
  --fds-yellow: #efd96f;
  --fds-blue: #92e7fa;
  --fds-dark: #333333;
}

body {
  background-color: var(--bg-dark);
}

.baseShadow {
  box-shadow: 0px 1px 2px -1px rgba(0, 0, 0, 0.8);
  -webkit-box-shadow: 0px 1px 2px -1px rgba(0, 0, 0, 0.8);
  -moz-box-shadow: 0px 1px 2px -1px rgba(0, 0, 0, 0.8);
  -o-box-shadow: 0px 1px 2px -1px rgba(0, 0, 0, 0.8);
}

.baseGlow {
  box-shadow: 0px 0px 10px #26829b, #26829b 0px 0px 10px inset;
}

.baseGlowText {
    text-shadow: 0px 0px 20px #26829b;
}

a {
  text-decoration: none !important;
}

.mainTitle1 {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
  font-size: 28px;
  letter-spacing: 0;
  line-height: 36px;
}

.mainTitle2 {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
  font-size: 28px;
  letter-spacing: 0;
  line-height: 36px;
}
.mainTitle3 {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
  font-size: 26px;
  letter-spacing: 0px;
  line-height: 32px;
}
.mainTitle4 {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
  font-size: 22px;
  letter-spacing: 0;
  line-height: 27px;
}
.bodyDefault {
  font-family: Montserrat, sans-serif;
  font-weight: 300;
  font-size: 16px;
  letter-spacing: 0;
  line-height: 19px;
}
.bodyBold {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
  font-size: 16px;
  letter-spacing: 0;
  line-height: 19px;
}
.bodyDetail {
  font-family: Montserrat, sans-serif;
  font-weight: 200;
  font-size: 20px;
  letter-spacing: 0;
  line-height: 24px;
}

.smallLight {
  font-family: Montserrat, sans-serif;
  font-weight: 300;
  font-size: 12px;
  letter-spacing: 0;
  line-height: 15px;
}

.smallBold {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
  font-size: 12px;
  letter-spacing: 0;
  line-height: 15px;
}
.smallestLight {
  font-family: Montserrat, sans-serif;
  font-weight: 300;
  font-size: 10px;
  letter-spacing: 0;
  line-height: 13px;
}
.smallestBold {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
  font-size: 10px;
  letter-spacing: 0;
  line-height: 13px;
}

.fair-drive {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  background-color: #333333;
}

.container {
  min-height: 100vh;
  width: 100vw;
  display: flex;
}

.topbar {
  position: fixed;
  top: 0px;
  height: 25vh;
  background-color: var(--bg-dark);
  color: var(--text-title);
  width: 100vw;
  background-color: var(--bg-dark);
}

.brand {
  composes: mainTitle2;
  margin-top: 120px;
}

.loginregistertitle {
  composes: bodyBold;
  color: #cecece !important;
  line-height: 36px;
}

/* Files */
.breadcrumb {
  composes: mainTitle2;
}

.h1 {
  composes: mainTitle4;
  color: var(--text-light);
}

.address {
  composes: bodyDefault;
  color: white;
}

.space {
  margin-top: 50px;
  margin-bottom: 20px;
}

.breadcrumbpath {
  composes: bodyBold;
  color: #cecece !important;
}

.exiticonblue {
  background-position: -270px -72px;
  background-image: url("./images/FairDrive-Sprite-doublesize.png");
  background-repeat: no-repeat;
  background-size: 324px 324px;
  width: 36px;
  height: 36px;
}

.exitgrayicon {
  background-image: url("./images/FairDrive-Sprite-doublesize.png");
  background-repeat: no-repeat;
  background-size: 324px 324px;
  background-position: -216px -234px;
  width: 36px;
  height: 36px;
}

.closeicon {
  background-image: url("./images/FairDrive-Sprite-doublesize.png");
  background-repeat: no-repeat;
  background-size: 324px 324px;
  background-position: -144px -234px;
  width: 36px;
  height: 36px;
}

.innercontainer {
  margin-top: 25vh;
  overflow: scroll;
  height: 75vh;
  background-color: var(--bg-dark);
  -webkit-overflow-scrolling: touch;
}

.logincontainer {
  margin-top: 140px;
  overflow: scroll;
  background-color: var(--bg-dark);
}

.register {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 20px 40px 20px 40px;
  width: 100vw;
}

.createnew {
  composes: smallLight;
  color: white !important;
  margin-top: 36px;
}

.createnew :hover {
  color: var(--fds-blue) !important;
}

.innercontainer input {
  font-family: Montserrat;
  font-size: 14px;
  color: white;
  width: 75vw;
  min-width: 200px;
  max-width: 300px;
  margin-top: 16px;
  background-color: none;
  border-bottom: 1px solid var(--bg-shade);
  background: transparent;
  border-top: transparent !important;
  border-left: transparent !important;
  border-right: transparent !important;
  line-height: 26px;
}

.innercontainer input:focus {
  outline: none;
  border-bottom: 1px solid var(--fds-blue);
}

.button {
  composes: bodyBold;
  composes: baseGlow;
  width: 75vw;
  min-width: 200px;
  max-width: 300px;
  text-align: center;
  box-sizing: border-box;
  padding: 20px 42px;
  border-radius: 4px;
  cursor: pointer;
  max-height: 62px;
  margin-top: 20px;
  border: 1px solid var(--fds-blue);
  margin-top: 40px;
}

.buttontext {
    composes: baseGlowText;
    text-transform: capitalize;
    color: var(--fds-blue);

}


.rowfile {
  display: grid;
  grid-template-columns:  min-content auto;
  grid-gap: 20px;
  width: 100vw;
  box-sizing: border-box;
  align-items: center;
}

.rowfile img {
  width: 24px;
  height: 24px;
}

.dirName {
  composes: bodyBold;
  white-space: nowrap; 
  overflow: hidden;
  text-overflow: ellipsis; 
  color: var(--text-light);
}

.icons8folder {
  display: inline-block;
  width: 26px;
  height: 26px;
  background: url("data:image/svg+xml;base64,")
    50% 50% no-repeat;
  background-size: 100%;
  margin-right: 14px;
}

.directoryrow {
  display: flex;
  flex-direction: row;
  background-color: var(--bg-dark);
  border-bottom: 1px solid var(--bg-shade);
  padding: 20px 40px;

}

.flaticon::before {
  font-family: "Flaticons Solid";
  content: "\E5B3";
}

@font-face {
  font-family: "Flaticons Solid";
  src: url(/fonts/flaticons-solid.eot);
  src: url(/fonts/flaticons-solid.eot?#iefix) format("embedded-opentype"),
    url(/fonts/flaticons-solid.woff) format("woff"),
    url(/fonts/flaticons-solid.ttf) format("truetype"),
    url(/fonts/flaticons-solid.svg#flaticons-solid) format("svg");
  font-weight: 400;
  font-style: normal;
}

.flaticon.solid:before {
  font-family: "Flaticons Solid";
}

.flaticon.solid.A1:before,
.flaticon.solid.grid-1:before,
.flaticon.solid.grid:before {
  content: "\E000";
}

.location {
  padding: 0px 40px;
  margin: 0px;
}

/* User */
.myaccount {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 30px 0px 10px 40px;
}

.handlebalance {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.handle {
  composes: smallBold;
  color: white;
}

.balance {
  composes: smallBold;
  color: var(--fds-blue);
}

.bigbalance {
  composes: mainTitle2;
  color: var(--fds-blue);
}
